.header {
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  height: 84px;
  background: $white;
  @include center;
  z-index: 15;
  &.active {
    z-index: 99;
    a {
      z-index: 999; }
    &:before {
      content: '';
      width: 100%;
      height: calc(100% + 1px);
      position: absolute;
      left: 0;
      top: 0;
      background: rgba(#000000, 0.8); } }
  h1 {
    font-size: 25.5px;
    font-weight: 600;
    line-height: 0.78;
    text-align: center;
    color: #131c2e; }
  a {
    position: absolute;
    top: 30px;
    right: 22px;
    img {
      height: 35px; } } }
